.header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 30px;
}

.header__left {
    flex: 0.5;
    min-width: 70px;
    background-color: #ffffff;
    color: #808080;
    border-radius: 30px;
    padding: 10px;
    display: flex;
    align-items: center;
}
.header__left > input {
    border: none;
    width: 100%;
}

.header__right {
    display: flex;
    align-items: center;
}

.header__right > h4 {
    margin-left: 10px;
}

.avatar {
    height: 37px;
    width: 37px;
    border-radius: 50px;
}